import React, { useState, useContext } from 'react'
import Inputs from '../../util/Inputs'
import SideBar from '../headers/SideBar'
import propertyContext from '../../context/property/propertyContext'

export const MeterData2 = ({ history }) => {
    const [spn, setSpn ] = useState(false)
    const [pro, setPro ] = useState(false)
    const PropertyContext = useContext(propertyContext)
    const { singleProperty, operate } = PropertyContext;
   const data = singleProperty || operate

   if(!data){
    const redirect = e => {
        history.push('/')
        window.location.reload()
    }
    return redirect()
}
    
    const { customerName, contractNumber, customerPhone, batchId, cartonNumber, meterSeal,
            meterCondition, meterInstallationDate, installerName, installerVendor, installerPhone,
         customerAuthentication, googleMapCoords } = data;

    const img = `http://178.62.124.95:5000/uploads/photo_6091b26a1b629327b878db7e_1620167928863.jpg`
    const img2 = `http://178.62.124.95:5000/uploads/photo_6091b26a1b629327b878db7e_1620167865914.jpg`
    return (
        <div>
            <SideBar />
    
        <div className="container-wrapper">

        <div className="float" style={{marginTop: 20, paddingRight: 15}}>

        <h3 style={{margin: '10px 15px 5px'}}>Customer Information</h3>
        <button className="btn" style={{background: '#4f4846'}} onClick={() => 
            history.push('/meter-data')
        }>Next</button>

        </div>

        <div className="grid-3 white-back">
            <Inputs title="Customer Name" name={customerName} />
            <Inputs title="Contract Number" name={contractNumber} />
            <Inputs title="Customer Phone No" name={customerPhone} />
            
        </div>

            <div className="grid-3 white-back">
               <Inputs title="Batch ID" name={batchId} />
               <Inputs title="Carton Number" name={cartonNumber} />
               <Inputs title="Meter Seal No." name={meterSeal} />
               <Inputs title="Meter Condition" name={meterCondition} />
               <Inputs title="Meter Installation Date" name={meterInstallationDate} />
               <Inputs title="Customer Authentication" name={customerAuthentication} />
              
            </div>

            <h3 style={{margin: '10px 15px 5px'}}>Installer Details</h3>
            <div className="grid-3 white-back">
               <Inputs title="Installer Name" name={installerName} />
               <Inputs title="Installer Vendor" name={installerVendor} />
               <Inputs title="Installer Phone No." name={installerPhone} />
            </div>

            <div style={{ display: 'flex', justifyContent: 'space-evenly',
                         alignItems: 'center', marginTop: 20, marginBottom: 20
                }}>

                <button className="btn" onClick={() => setPro(true)}>View Before Installation Image</button>
                <button className="btn" onClick={() => setSpn(true)}>View After Installation Image</button>
            </div>
              

        </div>

        {pro && <div className="absolutes">
            <div className="img-views">
                <p className="close" title="Close" onClick={() => setPro(false)}>x</p>
                <p style={{position: 'absolute', padding: '10px', color: '#f2f2f2'}}>{googleMapCoords}</p>
                <img src={img} alt="Property Portrait" className="img-g" />
            </div>
        </div> }

        {spn && <div className="absolutes">
            <div className="img-views">
                <p className="close" title="Close" onClick={() => setSpn(false)}>x</p>
                <p style={{position: 'absolute', padding: '10px', color: '#f2f2f2'}}>{googleMapCoords}</p>
                <img src={img2} alt="Property Portrait" className="img-g" />
            </div>
        </div> }

        </div>
    )
}

export default MeterData2