import React, {Fragment, useState} from 'react'
import Headers from './Headers'

const SideBar = () => {
    const [ enumeration, setEnumeration ] = useState(false)
    const [ metering, setMetering ] = useState(false)
    const [ asset, setAsset ] = useState(false)


    return (
        <Fragment>
            <div className="side-bar">
                <h2 style={{textAlign: 'center', marginTop: 20}}>Logo</h2>
                <div className="item">
                    <ul className="ul-parent">
                    
                    <li className="lis">                            
                    <h4><i className="fa fa-user fa-big"></i>
                        Customer Enumeration 
                        <span className="arrows"> 
                        { enumeration ?
                           <i className="fas fa-chevron-up" onClick={() => setEnumeration(false)}></i> :
                           <i className="fas fa-chevron-down" onClick={() => setEnumeration(true)}></i> 
                        }
                    </span></h4>

                        <ul className={ enumeration ? "ul-child" : "ul-child hidden" }>
                            <li><a href="/new-captures">Newly Captured Property</a></li>
                            <li><a href="/update-capture">Updated Property Records</a></li>
                        </ul>

                    </li>


                    <li className="lis">                            
                    <h4><i className="fa fa-tablet fa-big"></i> Metering Installation
                    <span className="arrows"> 
                        { metering ?
                           <i className="fas fa-chevron-up" onClick={() => setMetering(false)}></i> :
                           <i className="fas fa-chevron-down" onClick={() => setMetering(true)}></i> 
                        }
                    </span></h4>

                        <ul className={ metering ? "ul-child" : "ul-child hidden" }>
                            <li><a href="/drag">Upload Metering Schedule</a></li>
                            <li><a href="/meter-view">View Installed Meter</a></li>
                        </ul>

                    </li>

                    <li className="lis">                            
                    <h4> <i className="fas fa-tachometer-alt fa-big"></i>  Asset Capturing
                    <span className="arrows"> 
                        { asset ?
                           <i className="fas fa-chevron-up" onClick={() => setAsset(false)}></i> :
                           <i className="fas fa-chevron-down" onClick={() => setAsset(true)}></i> 
                        }
                    </span></h4>

                        <ul className={ asset ? "ul-child" : "ul-child hidden" }>
                            <li><a href="/transformer-views">Captured Transfomer</a></li>
                            <li><a href="/feeder-views">Captured Feeder</a></li>
                        </ul>

                    </li>



                    </ul>
                </div>
            </div>
            <Headers />
        </Fragment>
    )
}

export default SideBar
