import React, { useState } from 'react'

const ErrorAlert = ({ errorMsg, successMsg, cancel=false }) => {
    const msg = successMsg || errorMsg;
    const [ terminate, setTerminate ] = useState(false)

    const onClick = e => {
        if(cancel){
            setTerminate(true)
            window.location.reload()
        }
    }

    if(terminate){
        return ""
    }

   
    return (
        <div>
                {msg && <div className="absolutes">
                    <div className="alert " style={{borderRadius: 3}}>
                       <div className="cancel-parent">
                           <div></div>
                           <p className="cancel" onClick={onClick}>x</p>
                       </div>

                    <div className="float" style={{flexDirection: 'column', padding: 20,
                        alignItems: 'center', justifyContent: 'center'
                    }}>
                        {errorMsg && <i className="" style={{fontSize:25}}></i>}
                        {successMsg && <i className="fa fa-check-circle success" style={{fontSize:25}}></i>}
                          <h1 className="sm">{msg}</h1>
                       </div>
                    </div>
                </div> }

        </div>
    )
}

export default ErrorAlert
