import React, { useState, useContext, useEffect } from 'react'
import SideBar from '../headers/SideBar'
import Alert from '../essentials/Alert';
import { CSVLink } from 'react-csv'
import { meteringHeaderDownload } from '../essentials/DownloadHeaders';
import { NewCaptureDownload } from '../essentials/NewCapture';
import propertyContext from '../../context/property/propertyContext'

function MeterViews({ history }) {
const PropertyContext = useContext(propertyContext)
const { getMeteringDetail, metering, createOperating, errors } = PropertyContext;

const [ reveal, setReveal ] =  useState(false)

if(errors === 'No signature! Access denied'){
    history.push('/')
    window.location.reload()
}

useEffect(() => {
    setReveal(true)
    getMeteringDetail()
    // eslint-disable-next-line
}, [])

    const onClick = e => {
        createOperating(metering[e.target.name])
        history.push('/meter-data')
    }

    if(metering === null){
        return <Alert msg="Please Wait..." loading={reveal} 
        response={errors ? errors : null}
            btnMsg={errors ? "Go Back": ""} page={ errors ? "/" : ""} 
            icon={ errors ? "fa fa-times-circle danger": ""}
            iconed={errors ? "fa fa-chevron-left" : "" }
        />
    }

    if(metering.length < 1){
        return <div>
            <SideBar />
            <div className="container-wrapper">
        <div style={{display: 'flex', height: '60vh', justifyContent: 'center', alignItems: 'center'}}>
                <h1 style={{color: '#ccc'}}>No Record Found - 404 </h1>
            </div>
        </div>
        </div>
    }
    
    const headersAndName = meteringHeaderDownload()
    const result = NewCaptureDownload(metering[0], metering, headersAndName.headers, headersAndName.fname)

    return (
        <div>
            <SideBar />
            
        <div className="container-wrapper">
            <div className="new-capture">
                <h2 style={{marginBottom: 5, float: 'left'}}>Meter Installation Status</h2>
            <table>
                <thead>
                    <tr>
                        <th>Batch ID</th>
                        <th className="th-wide">Contract Name</th>
                        <th>Contract No.</th>
                        <th className="th-wide">Building Address</th>
                        <th>Phone No.</th>
                        <th>Phase Type</th>
                        <th>Area Office</th>
                        <th>Center</th>
                        <th>Vendor</th>
                        <th>Installer</th>
                        <th>More</th>                        
                    </tr>
                </thead>
                <tbody>
                   
                    { metering.map((data, index) => <tr key={index}>
                        <td>{data.batchId}</td>
                        <td>{data.customerName}</td>
                        <td>{data.contractNumber}</td>
                        <td>{data.meterAddress}</td>
                        <td>{data.customerPhase}</td>
                        <td>{data.phase}</td>
                        <td>{data.googleMapCoords}</td>
                        <td>{data.region}</td>
                        <td>{ data.office }</td>
                        <td>{data.installerName}</td>
                        <td>{
                             <button className="stx" onClick={onClick} style={{background: '#f2f2f2'}} 
                                    name={index}
                             >
                             {data.installationStatus === true ? 
                             <i className=" fa fa-check-circle success" style={{
                                   marginRight: 2
                               }}></i>
                                      :
                               <i className="fa fa-times-circle danger" style={{
                                   marginRight: 2
                               }}></i> }
                                {data.installationStatus === false ? "Installed" : "Pending"} 
                            </button>
                        }</td>
                    </tr> )}
                </tbody>
            </table>

            {/* <button className="btn" onClick={() => console.log("Hi")} style={{marginTop: 20}}>Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </button> */}


            <div style={{marginTop: 20}}>
            <CSVLink className="btn" { ...result }> Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </CSVLink>
            </div>



            </div>
        </div>

        </div>
    )
}

export default MeterViews
