import React, { useState, useContext } from 'react'
import Inputs from '../../util/Inputs'
import propertyContext from '../../context/property/propertyContext'
import SideBar from '../headers/SideBar'

export const NewlyCaptured = ({ history }) => {
    const PropertyContext = useContext(propertyContext)
     const { singleProperty, operate } = PropertyContext;
    const data = singleProperty || operate

    const [spn, setSpn ] = useState(false)
    const [pro, setPro ] = useState(false)

    if(!data){
        const redirect = e => {
            history.push('/')
            window.location.reload()
        }
        return redirect()
    }

    const {googleMapCoords, locationCoords, buildingAddress, route, itinerary, 
        serviceCenter, volt, distanceFromSubstation, buildingType, buildingUse,
       buildingActivity, bookCode, feederName,phase, region, office, connectionType,
        poleNumber, transformerName, areaType, occupantName, occupantRole, propertyId,
        propertyOwnerName, propertyServicePointImage, propertyImage
     } = data;

    const img = `http://178.62.124.95:5000/uploads/${propertyImage}`
    const img2 = `http://178.62.124.95:5000/uploads/${propertyServicePointImage}`
    return (
        <div>
            <SideBar />
    
        <div className="container-wrapper">
             <h3 style={{margin: '10px 15px 5px'}}>Property Description</h3>
            <div className="grid-3 white-back">
               <Inputs title="Occupant Name" name={occupantName} />
               <Inputs title="Occupant Role" name={occupantRole} />
               <Inputs title="Property Owner Name" name={propertyOwnerName} />
               <Inputs title="Building Type" name={buildingType} />
               <Inputs title="Building Activity" name={buildingActivity} />
               <Inputs title="Building Use" name={buildingUse} />
            </div>

            <h3 style={{margin: '10px 15px 5px'}}>Property Location</h3>
            <div className="grid-3 white-back">
                <Inputs title="Building Address" name={buildingAddress} />
               <Inputs title="Google Map Coords" name={googleMapCoords} />
               <Inputs title="Location Coords" name={locationCoords} />
               <Inputs title="Region" name={region} />
               <Inputs title="Area Office" name={office} />
               <Inputs title="Service Center" name={serviceCenter} />

               <Inputs title="Route" name={route} />
               <Inputs title="Itinerary" name={itinerary} />
               <Inputs title="Property Id" name={propertyId} />

            </div>

            <h3 style={{margin: '10px 15px 5px'}}>Network Description</h3>
            <div className="grid-3 white-back">
               <Inputs title="Connection Type" name={connectionType} />
               <Inputs title="Supply Transformer" name={transformerName} />
               <Inputs title="Premises Distance from S/S" name={distanceFromSubstation} />
               <Inputs title="Pole Number" name={poleNumber} />
               <Inputs title="Volt" name={volt} />
               <Inputs title="Phase" name={phase} />
            </div>

            <div className="grid-3 white-back">
               <Inputs title="Book Code" name={bookCode} />
               <Inputs title="Feeder Name" name={feederName} />
               <Inputs title="Area Type" name={areaType} />
            </div>

            <div style={{ display: 'flex', justifyContent: 'space-evenly',
                         alignItems: 'center', marginTop: 20, marginBottom: 20
                }}>

                <button className="btn" onClick={() => setPro(true)}>View Property Image</button>
                <button className="btn" onClick={() => setSpn(true)}>View Service Point Image</button>
            </div>
              

        </div>

        {pro && <div className="absolutes">
            <div className="img-views">
            <p className="close" title="Close" onClick={() => setPro(false)}>x</p>
            <p style={{position: 'absolute', padding: '10px', color: '#f2f2f2'}}>Coords</p>
                <img src={img} alt="Property Portrait" className="img-g" />
            </div>
        </div> }

        {spn && <div className="absolutes">
            <div className="img-views">
            <p className="close" title="Close" onClick={() => setSpn(false)}>x</p>
            <p style={{position: 'absolute', padding: '10px', color: '#f2f2f2'}}>Coords</p>
                
                <img src={img2} 
                alt="Property Portrait" className="img-g" />

            </div>
        </div> }

        </div>
    )
}
