import React, { useContext } from 'react'
import Inputs from '../../util/Inputs'
import SideBar from '../headers/SideBar'
import propertyContext from '../../context/property/propertyContext'

export const MeterData = ({ history }) => {

    const PropertyContext = useContext(propertyContext)
    const { singleProperty, operate } = PropertyContext;
   const data = singleProperty || operate

   console.log(data)

   if(!data){
    const redirect = e => {
        history.push('/')
        window.location.reload()
    }
    return redirect()
}
    
    const { meterAddress, googleMapCoords, meterPosition, region, serviceCenter, office,
            meterType, meterNumber, meterModel, meterProvider, meterOwner, phase} = data;

      return (
        <div>
            <SideBar />

        <div className="container-wrapper">
            <div className="float" style={{marginTop: 20, paddingRight: 15}}>

                <h3 style={{margin: '10px 15px 5px'}}>Meter Location</h3>
                <button className="btn" style={{background: '#4f4846'}} onClick={() => 
                    history.push('/meter-datas')
                }>Previous</button>

            </div>

            <div className="grid-3 white-back">
                <Inputs title="Meter Address" name={meterAddress} />
               <Inputs title="Google Map Coords" name={googleMapCoords} />
               <Inputs title="Meter Position" name={meterPosition} />
               <Inputs title="Region" name={region} />
               <Inputs title="Area Office" name={office} />
               <Inputs title="Service Center" name={serviceCenter} />
            </div>

            <h3 style={{margin: '10px 15px 5px'}}>Meter Description</h3>
            <div className="grid-3 white-back">
               <Inputs title="Meter Type" name={meterType} />
               <Inputs title="Meter Number" name= {meterNumber} />
               <Inputs title="Meter Model" name={meterModel} />
               <Inputs title="Meter Provider" name={meterProvider} />
               <Inputs title="Meter Owner" name={meterOwner}/>
               <Inputs title="Meter Phase" name= {phase}/>
            </div>

        </div>

        </div>
    )
}

export default MeterData