import { CLEARERRORS, CLEARSUCCESSMSG, GETFEEDER, GETFEEDERFAIL, GETMETERINGDETAIL, GETMETERINGDETAILFAIL, GETPROPERTY, GETPROPERTYFAIL, 
    GETTRANSFORMER, 
    GETTRANSFORMERFAIL, 
    LOGIN, 
    LOGINFAIL, 
    OPERATING, SINGLEPROPERTY, SINGLEPROPERTYFAIL, UPDATEPROPERTY, UPDATEPROPERTYFAIL } from "./type"


const PropertyReducer =  ( state, action) => {
    switch( action.type ){
        case GETPROPERTY:
            return{
                ...state,
                properties: action.payload,
                loading: false
            }
        case SINGLEPROPERTY: 
            return{
                ...state,
                singleProperty: action.payload,
                loading: false
            }
        case OPERATING:
            return{
                ...state,
                operate: action.payload
            }

        case UPDATEPROPERTY:
            return{
                ...state,
                successMsg: action.payload
            }
        case GETMETERINGDETAIL:
            return{
                ...state,
                metering: action.payload,
                loading: false
            }
        case GETTRANSFORMER: 
            return{
                ...state,
                transformer: action.payload,
                loading: false
            }
        case GETFEEDER:
            return {
                ...state,
                feeder: action.payload,
                loading: false
            }

            case CLEARSUCCESSMSG:
                return {
                    ...state,
                    successMsg: null,
                    loading: true
                }

            case GETPROPERTYFAIL:
            case SINGLEPROPERTYFAIL:
            case UPDATEPROPERTYFAIL:
            case GETMETERINGDETAILFAIL:
            case GETTRANSFORMERFAIL:
            case GETFEEDERFAIL:
            case LOGINFAIL:
                return{
                    ...state,
                    errors: action.payload
                }

            case CLEARERRORS: 
            return{
                ...state,
                errors: null
            }



        case LOGIN:
            return{
                ...state,
                login: action.payload,
                loading: false
            }


        default:
        return state

    }
}

export default PropertyReducer