import React from 'react'
import SideBar from '../headers/SideBar'
import { CSVLink } from 'react-csv'
import { fdrHeaderDownload } from '../essentials/DownloadHeaders';
import { NewCaptureDownload } from '../essentials/NewCapture';
import propertyContext from '../../context/property/propertyContext'
import Alert from '../essentials/Alert';

function FeederViews({ history }) {
    const PropertyContext = React.useContext(propertyContext)
    const { getFeeder, feeder, createOperating, errors } = PropertyContext;
    
    const [ reveal, setReveal ] =  React.useState(false)

    if(errors === 'No signature! Access denied'){
        history.push('/')
        window.location.reload()
    }
    
    React.useEffect(() => {
        setReveal(true)
        getFeeder()
        // eslint-disable-next-line
    }, [])
    
        const onClick = e => {
            createOperating(feeder[e.target.name])
            history.push('/feeder-captured')
        }

        console.log(errors)
    
        if(feeder === null){
            return <Alert msg="Please Wait..." loading={reveal} 
            response={errors ? errors : null}
            btnMsg={errors ? "Go Back": ""} page={ errors ? "/" : ""} 
            icon={ errors ? "fa fa-times-circle danger": ""}
            iconed={errors ? "fa fa-chevron-left" : "" }
            />
        }
    
        if(feeder.length < 1){
            return <div>
                <SideBar />
                <div className="container-wrapper">
            <div style={{display: 'flex', height: '60vh', justifyContent: 'center', alignItems: 'center'}}>
                    <h1 style={{color: '#ccc'}}>No Record Found - 404 </h1>
                </div>
            </div>
            </div>
        }
        
        const headersAndName = fdrHeaderDownload()
        const result = NewCaptureDownload(feeder[0], feeder, headersAndName.headers, headersAndName.fname)


    return (
        <div>
            <SideBar />
            
        <div className="container-wrapper">
            <div className="new-capture">
                <h2 style={{marginBottom: 5}}>Captured Feeder</h2>
            <table>
                <thead>
                    <tr>
                        <th>Code</th>
                        <th>Name</th>
                        <th>Address</th>
                        <th>Capacity</th>
                      
                        <th>Hourly Supply</th>
                        <th>Google Map Coords</th>
                        <th>Office</th>
                        
                        <th>More</th>                        
                    </tr>
                </thead>
                <tbody>
                { feeder.map((data, index) => <tr key={index}>
                        <td>{data.code}</td>
                        <td>{data.feederName}</td>
                        <td>{data.feederAddress}</td>
                        <td>{data.feederCapacity}</td>
                        <td>{data.averageHourlySupply}</td>
                        <td>{data.googleMapCoords}</td>
                        <td>{data.office}</td>
                        <td>{
                            <button className="stx" name={index} onClick={onClick}> View </button>
                        }</td>
                    </tr> )}
                </tbody>
            </table>

            {/* <button className="btn" onClick={() => console.log("Hi")} style={{marginTop: 20}}>Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </button> */}


            <div style={{marginTop: 20}}>
            <CSVLink className="btn" { ...result }> Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </CSVLink>
            </div>



</div>

            </div>
        </div>
    )
}

export default FeederViews
