import React, { useReducer } from 'react'
import PropertyContext from './propertyContext'
import PropertyReducer from './propertyReducer'
import axios from 'axios'
import { CLEARERRORS, CLEARSUCCESSMSG, GETMETERINGDETAIL, GETPROPERTY, GETPROPERTYFAIL, LOGINFAIL,
    OPERATING, SINGLEPROPERTY, SINGLEPROPERTYFAIL, UPDATEPROPERTY, GETTRANSFORMERFAIL,
    UPDATEPROPERTYFAIL, GETMETERINGDETAILFAIL, GETTRANSFORMER, GETFEEDER, GETFEEDERFAIL, LOGIN } from './type'
import setAuth from '../../util/setAuth'


const PropertyState = ({ children }) => {
    const url = 'http://localhost:5000/api/v1'
    const initialState = {
        properties: null,
        singleProperty: null,
        metering: null,
        operate: null,
        successMsg: null,
        loading: true,
        errors: null,
        transformer: null,
        feeder: null,
        login: null
    }

    const [ state, dispatch ] = useReducer(PropertyReducer, initialState);

    // All function
    const getSingleProperty = async e => {
        if(localStorage.token){
            setAuth(localStorage.token)
        }
        await axios.get(`${url}/property/${e}`)
        .then(response => dispatch({ type: SINGLEPROPERTY, payload: response.data.data}))
        .catch(err => {
            dispatch({ type: SINGLEPROPERTYFAIL, payload: err.response.data.error})
            // setTimeout(clearErrors, 6000);
        })
    }

    // Get all Properties
    const getProperties = async e => {
        await axios.get(`${url}/property?${e}`)
        .then(response => dispatch({ type: GETPROPERTY, payload: response.data.data}))
        .catch(err => {
            dispatch({ type: GETPROPERTYFAIL, payload: err.response.data.error})
            // setTimeout(clearErrors, 6000);
        })
    }


    // Update Property Info
    const UpdateProperty = async (id, data) => {
        await axios.put(`${url}/property?${id}`, data, { 
            headers: {
                "Content-Type": "application/json"
            }
        })
        .then(response => dispatch({ type: UPDATEPROPERTY, payload: response.data.data}))
        .catch(err => {
            dispatch({ type: UPDATEPROPERTYFAIL, payload: err.response.data.error})
            // setTimeout(clearErrors, 6000);
        })
    }

    // Clear Error Messages
    const loggedOrNot = e => dispatch({ type: LOGIN, payload: e })
    const loggedError = e => dispatch({ type: LOGINFAIL, payload: e })

    // Clear Error Messages
    const clearErrors = e => dispatch({ type: CLEARERRORS })
    // Clear Success Msg
    const clearSucessMsg = e => dispatch({ type: CLEARSUCCESSMSG })

    const createOperating = e => dispatch({
        type: OPERATING,
        payload: e
    })

    // Get Metering Details
    const getMeteringDetail = async e => {
        const adds = e === undefined ? "" : e
        await axios.get(`${url}/metering?${adds}`)
        .then(response => dispatch({ type: GETMETERINGDETAIL, payload: response.data.data}))
        .catch(err => {
            dispatch({ type: GETMETERINGDETAILFAIL, payload: err.response.data.error})
            // setTimeout(clearErrors, 6000);
        })
    }

     // Get Metering Details
     const getTransformer = async e => {
        const adds = e === undefined ? "" : e
        await axios.get(`${url}/transformer?${adds}`)
        .then(response => dispatch({ type: GETTRANSFORMER, payload: response.data.data}))
        .catch(err => {
            dispatch({ type: GETTRANSFORMERFAIL, payload: err.response.data.error})
            // setTimeout(clearErrors, 6000);
        })
    }

     // Get Metering Details
     const getFeeder = async e => {
        const adds = e === undefined ? "" : e
        await axios.get(`${url}/feeder?${adds}`)
        .then(response => dispatch({ type: GETFEEDER, payload: response.data.data}))
        .catch(err => {
            dispatch({ type: GETFEEDERFAIL, payload: err.response.data.error})
            setTimeout(clearErrors, 6000);
        })
    }




    return <PropertyContext.Provider
        value={{
            properties: state.properties,
            singleProperty: state.singleProperty,
            loading: state.loading,
            successMsg: state.successMsg,
            operate: state.operate,
            metering: state.metering,
            transformer: state.transformer,
            feeder: state.feeder,
            errors: state.errors,
            login: state.login,
            getFeeder,
            getTransformer,
            getMeteringDetail,
            getSingleProperty,
            getProperties,
            UpdateProperty,
            clearErrors,
            clearSucessMsg,
            createOperating,
            loggedOrNot,
            loggedError
        }}
    >
        { children }
    </PropertyContext.Provider>
}


export default PropertyState