import React from 'react'

function Inputs({ title, name=""}) {
    const onChange = e => {}
    return (
        <div>
            <form>
                        <h4 style={{marginBottom: '5px', fontSize: 14}}>{title}</h4>
                        <input type="text" disabled onChange={onChange} value={name}/>
                    </form>
        </div>
    )
}

export default Inputs
