import React, { useState, useContext, useEffect } from 'react'
import Alert from '../essentials/Alert';
import { CSVLink } from 'react-csv'
import { propertyHeaderDownload } from '../essentials/DownloadHeaders';
import { NewCaptureDownload } from '../essentials/NewCapture';
import propertyContext from '../../context/property/propertyContext'
import SideBar from '../headers/SideBar';

function UpdateCapture({ history }) {
    const PropertyContext = useContext(propertyContext)
    const { getProperties, properties, createOperating, errors } = PropertyContext;

    const [ reveal, setReveal ] =  useState(false)

    if(errors === 'No signature! Access denied'){
        history.push('/')
        window.location.reload()
    }

    useEffect(() => {
        setReveal(true)
        getProperties(`enumerationStatus=Existing`)
        // eslint-disable-next-line
    }, [])


    const onClick = e => {
        createOperating(properties[e.target.name])
        history.push('/newly-captured')
    }

    if(properties === null){
        return <Alert msg="Please Wait..." loading={reveal} 
        response={errors ? errors : null}
            btnMsg={errors ? "Go Back": ""} page={ errors ? "/" : ""} 
            icon={ errors ? "fa fa-times-circle danger": ""}
            iconed={errors ? "fa fa-chevron-left" : "" }
        />
    }

    if(properties.length < 1){
        return <div>
            <SideBar />
            <div className="container-wrapper">
        <div style={{display: 'flex', height: '60vh', justifyContent: 'center', alignItems: 'center'}}>
                <h1 style={{color: '#ccc'}}>No Record Found - 404 </h1>
            </div>
        </div>
        </div>
    }
    
    const headersAndName = propertyHeaderDownload()
    const result = NewCaptureDownload(properties[0], properties, headersAndName.headers, headersAndName.fname)

    return (
        <div>
            <SideBar />
        <div className="container-wrapper">
            <div className="new-capture">
                <h2 style={{marginBottom: 5}}>Updated Property Records</h2>
            <table>
                <thead>
                    <tr>
                        <th>Property ID</th>
                        <th className="th-wide">Account Name</th>
                        <th>Contract No.</th>
                        <th>Building Activity</th>
                        <th>Building Use</th>
                        <th className="th-wide">Building Address</th>
                        <th>Coordinate</th>
                        <th>Region</th>
                        <th>Business Unit</th>
                        <th>Business Center</th>
                        <th>More</th>                        
                    </tr>
                </thead>
                <tbody>
                { properties.map((data, index) => <tr key={index}>
                        <td>{data.propertyId}</td>
                        <td>{data.occupantName}</td>
                        <td>{data.buildingType}</td>
                        <td>{data.buildingActivity}</td>
                        <td>{data.buildingUse}</td>
                        <td>{data.buildingAddress}</td>
                        <td>{data.googleMapCoords}</td>
                        <td>{data.region}</td>
                        <td>{ data.office }</td>
                        <td>{data.serviceCenter}</td>
                        <td>{
                            <button className="stx" name={index} onClick={onClick}> View </button>
                        }</td>
                    </tr> )}
                </tbody>
            </table>

            {/* <button className="btn" onClick={() => console.log("Hi")} style={{marginTop: 20}}>Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </button> */}

            <div style={{marginTop: 20}}>
            <CSVLink className="btn" { ...result }> Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </CSVLink>
            </div>


            </div>
            </div>
        </div>
    )
}

export default UpdateCapture
