import axios from 'axios'
import React, { Fragment, useContext, useState } from 'react'
import propertyContext from '../../context/property/propertyContext'
import Alert from '../essentials/Alert'
import ErrorAlert from '../essentials/ErrorAlert'

const Headers = () => {
    const PropertyContext = useContext(propertyContext)
    const { getSingleProperty, loading, userImage } = PropertyContext

    const [ propertyId, setPropertyId ] =  useState("")
    const [ reveal, setReveal ] =  useState(false)
    const [ empty, setEmpty ] =  useState(false)
    const [ logout, setLogout ] =  useState(false)

    const onChange = e => setPropertyId(e.target.value)
    const reloadAll = e => window.location.reload();

    const searchById = e => {
        if(propertyId){
            setReveal(true)
            getSingleProperty(propertyId)
        }else{
            setEmpty(true)
        }
    }


    if( reveal ){
        return <Alert msg="Please Wait..." loading={reveal} page={loading ? null : 'newly-captured'} 
        btnMsg="View Result" />
    }

    if( empty ){
        return <ErrorAlert errorMsg="You have not entered a property ID" cancel={true}/>
    }

    return (
    <Fragment>
        <div className="container-wrapper">
            <div className="header">
                <div className="header-flex">

                    <div className="search-input">
                        <input type="text" onChange={onChange} className="short customize-short" 
                            placeholder="Search by Property ID"
                            value={propertyId}
                        />
                        <button className="btn" onClick={searchById}>Search</button>
                    </div>

                    <div className="not-parent">
                        <div className="notification">
                        <button className="sbtn" onClick={reloadAll}>
                            <i className="fa fa-sync" style={{
                                paddingRight: 6, fontSize: 18, color: 'white'
                            }}>

                            </i>
                            Update Table
                        </button>

                        <button className="remove-backs cursor-r"> <i className="fa fa-bell" 
                                style={{ fontSize: 20, cursor: 'pointer'
                                }}>
                            </i> </button>
                            
                        <div className='avatar'>
                            { userImage ? <img className="img"
                                src={`http://178.62.124.95:5000/uploads/${userImage}`}
                                alt="Avatar" /> :
                                <i className="fa fa-user" ></i>
                             }
                        </div>  

                            <button className="remove-backs cursor-r"> <i className="fa fa-ellipsis-v" 
                                style={{ fontSize: 20, cursor: 'pointer'
                                }} onClick={() => setLogout(true)}>
                            </i> 
                            
                                { logout && <div style={{width: '200px', height: '100px', background: 'white',
                                    padding: 20, position: 'absolute', right: 10,
                                    boxShadow: 'rgba(99, 99, 99, 0.2) 0px 2px 8px 0px'
                                }}>

                                    <h4 style={{
                                        textAlign: 'left', fontSize: 15, fontWeight: '400',
                                        textDecoration: 'underline', color: 'darkblue'
                                    }}
                                        onClick={async () => {
                                           await axios.get('http://localhost:5000/api/v1/user/login')
                                           .then(response => console.log(response.data.data))
                                           .catch(err => console.log(err.response.data.error))
                                           setLogout(false)
                                        }}
                                    
                                    >Logout</h4>

                                </div> }
                            
                            </button>  
                           
                        </div>
                    </div>


                </div>
        </div>
        </div> 
    </Fragment>
    )
}

export default Headers
