import React from 'react'
import SideBar from '../headers/SideBar'
import { CSVLink } from 'react-csv'
import { tfrHeaderDownload } from '../essentials/DownloadHeaders';
import { NewCaptureDownload } from '../essentials/NewCapture';
import propertyContext from '../../context/property/propertyContext'
import Alert from '../essentials/Alert';

function TransformerViews({ history }) {

    const PropertyContext = React.useContext(propertyContext)
    const { getTransformer, transformer, createOperating, errors } = PropertyContext;
    
    const [ reveal, setReveal ] =  React.useState(false)

    if(errors === 'No signature! Access denied'){
        history.push('/')
        window.location.reload()
    }
    
    React.useEffect(() => {
        setReveal(true)
        getTransformer()

        // eslint-disable-next-line
    }, [])
    
        const onClick = e => {
            createOperating(transformer[e.target.name])
            history.push('/trans-captured')
        }
    
        if(transformer === null){
            return <Alert msg="Please Wait..." loading={reveal} 
            response={errors ? errors : null}
            btnMsg={errors ? "Go Back": ""} page={ errors ? "/" : ""} 
            icon={ errors ? "fa fa-times-circle danger": ""}
            iconed={errors ? "fa fa-chevron-left" : "" }
            />
        }
     
        if(transformer.length < 1){
            return <div>
                <SideBar />
                <div className="container-wrapper">
            <div style={{display: 'flex', height: '60vh', justifyContent: 'center', alignItems: 'center'}}>
                    <h1 style={{color: '#ccc'}}>No Record Found - 404 </h1>
                </div>
            </div>
            </div>
        }
        
        const headersAndName = tfrHeaderDownload()
        const result = NewCaptureDownload(transformer[0], transformer, headersAndName.headers, headersAndName.fname)
    

    return (
        <div>
            <SideBar />
        <div className="container-wrapper">
            <div className="new-capture">
                <h2 style={{marginBottom: 5}}>Captured Transformer</h2>
            <table>
                <thead>
                    <tr>
                        <th>Code</th>
                        <th>Name</th>
                        <th>Rating</th>
                        <th>Capacity</th>
                        <th>Loss (%)</th>
                        <th>Hourly Supply</th>
                        <th>Coordinate</th>
                        <th>Center</th>
                        <th>More</th>                        
                    </tr>
                </thead>
                <tbody>
                { transformer.map((data, index) => <tr key={index}>
                        <td>{data.code}</td>
                        <td>{data.transformerName}</td>
                        <td>{data.transformerRating}</td>
                        <td>{data.transformerCapacity}</td>
                        <td>{data.inpedentLoss}</td>
                        <td>{data.averageHourlySupply}</td>
                        <td>{data.googleMapCoords}</td>
                        <td>{data.serviceCenter}</td>
                        <td>{
                            <button className="stx" name={index} onClick={onClick}> View </button>
                        }</td>
                    </tr> )}
                </tbody>
            </table>

            {/* <button className="btn" onClick={() => console.log("Hi")} style={{marginTop: 20}}>Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </button> */}


            <div style={{marginTop: 20}}>
            <CSVLink className="btn" { ...result }> Download
                <i className='fa fa-download' style={{marginLeft: 10}}></i>
            </CSVLink>
            </div>



            </div>
        </div>

        </div>
    )
}

export default TransformerViews
