import React, { Fragment } from 'react'
import './App.css';
import SideBar from './component/layout/headers/SideBar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import MeterData from './component/layout/pages/MeterData';
import MeterData2 from './component/layout/pages/MeterData2.';
import FeederCaptured from './component/layout/pages/FeederCaptured.';
import TransCaptured from './component/layout/pages/TransCaptured';
import { NewlyCaptured } from './component/layout/pages/NewlyCaptured';
import FeederViews from './component/layout/routes/FeederViews';
import TransformerViews from './component/layout/routes/TransformerView';
import UpdateCapture from './component/layout/routes/UpdateCapture';
import DragNDrop from './component/layout/routes/DragNDrop';
import MeterViews from './component/layout/routes/MeterViews';
import NewCaptures from './component/layout/routes/NewCaptures';
import Alert from './component/layout/essentials/Alert';
import PropertyState from './component/context/property/PropertyState'
import ErrorAlert from './component/layout/essentials/ErrorAlert';
import Login from './component/layout/essentials/Login';


const App = () => {
  return (
    <PropertyState>
    <Router>

<Fragment>
      <Login />
      <ErrorAlert />
      <Switch>
          <Route exact path="/" component={ SideBar } />
          <Route exact path="/drag" component={ DragNDrop} />
          <Route exact path="/new-captures" component={ NewCaptures } />
          <Route exact path="/update-capture" component={ UpdateCapture } />
          <Route exact path="/meter-view" component={ MeterViews } />
          <Route exact path="/transformer-views" component={ TransformerViews } />
          <Route exact path="/feeder-views" component={ FeederViews } />
          <Route exact path="/newly-captured" component={ NewlyCaptured } />
          <Route exact path="/trans-captured" component={ TransCaptured } />
          <Route exact path="/feeder-captured" component={ FeederCaptured } />
          <Route exact path="/meter-datas" component={ MeterData2 } />
          <Route exact path="/meter-data" component={ MeterData } />
          <Route exact path="/no-where" component={ Alert } />

      </Switch>
    </Fragment>
    </Router>
    </PropertyState>

  );
}

export default App;
