import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const Alert = ({ msg, page, btnMsg, response, loading=false, icon, cancel, iconed }) => {
    const [ terminate, setTerminate ] = useState(false)
    const [ remove, setRemove ] = useState(false)
    const [ flash, setFlash ] = useState(false)

    const cancelled = e => {
        if(cancel){
            setTerminate(true)
            window.location.reload()
            setFlash()
        }
    }

    if(!loading){
        return ""
    }

    if(remove){
        return ""
    }

    let showRedirect;
    if(page && btnMsg){
        showRedirect = true
    }

    
    return (
        <div>
        { terminate ? "" : <div className="absolutes">
            <div className="alert float" style={{flexDirection: 'column', justifyContent: 'center'}}>
                   { flash && <p style={{
                        position: 'absolute', top: '31%', textDecoration: 'none', fontSize: 15,
                        background: 'darkred', padding: '0.5rem 0.8rem', color: 'white', left: `${133/2}%`,
                        borderRadius: 10
                    }} title="Close" onClick={cancelled}>x
                </p> }
                {!showRedirect ? <div className="float">
                    <div className="btn-spinner" style={{width: 30, height: 30,
                        borderTopColor: `#f1b24a`,
                        borderBottomColor: `#15032b`
                    
                    }}></div>

                    <div style={{paddingLeft: 10}}>
                        <h4 style={{fontSize: 13}}>{msg}</h4>
                    </div>

                </div> : 
                
                    <div className="float" style={{flexDirection: 'column', justifyContent: 'center'}}>
                        <i className={icon} style={{fontSize: 30, marginBottom: 5}}></i>
                         <h4 style={{fontSize: 16}}>{response}</h4>
                    </div>
                
                }

               {showRedirect && <Link to={`${page}`} className="btn" style={{marginTop: 20}}
                    onClick={() => setRemove(true)}
               >
                   {btnMsg} <i className={iconed} style={{marginLeft: 10}}></i>
                </Link> }

            </div>
        </div> }



        </div>
    )
}


export default Alert
