
// E.g: NewCaptureDownload(captured[0], captured, [{label: 'Hi'}], 'Report.csv' )

const NewCaptureDownload = (fields, downloadable, customizedHeaders, filename) => {
    const fieldName =  Object.keys(fields)

    const data = downloadable.map(file => {

        let result = {}
        fieldName.forEach(((data, index) => {
            result[`${data}`] = Object.values(file)[index]
        }))

        return result

    })

    // const headers = [
    //     { label: 'Contract Number', key: 'contractNumber' },
    //     { label: 'Customer Name', key: 'customerName'},
    //     { label: 'Customer Type', key: 'customerType'},
    //     { label: 'Connection Type', key: 'connectionType'},
    //     { label: 'Tariff Band', key: 'tariffDescription'},
    //     { label: 'Service Type', key: 'serviceType'},
    //     { label: 'Billing Type', key: 'billType'},
    //     { label: 'Billing Period', key: 'period'},
    //     { label: 'Consumption', key: 'consumption'},
    //     { label: 'Service Type', key: 'serviceType'},
    //     { label: 'Amount Billed', key: 'amountBilled'},
    //     { label: 'Balance', key: 'totalBalance'}
    // ]

    const csvReport = {
        filename: filename,
        headers: customizedHeaders,
        data: data
    }


    return csvReport;
    
}

export{
    NewCaptureDownload
}