import React, { useState } from 'react'
import Inputs from '../../util/Inputs'
import SideBar from '../headers/SideBar'
import propertyContext from '../../context/property/propertyContext'

 const TransCaptured = ({ history }) => {
    const PropertyContext = React.useContext(propertyContext)
    const { singleProperty, operate } = PropertyContext;
   const data = singleProperty || operate

   const [pro, setPro ] = useState(false)


   if(!data){
    const redirect = e => {
        history.push('/')
        window.location.reload()
    }
    return redirect()
}
    
    const { transformerName, transformerOwner, transformerRating, transformerCapacity, code,
            averageHourlySupply, inpedentLoss, region, office, serviceCenter, transformerAddress,
            googleMapCoords, dtType, dtMeterNo, connectionType, cappedUnit,
            feederName, bandDescription, transformerImage } = data;

    // const {  } = showData.property;

    const img = `http://178.62.124.95:5000/uploads/${transformerImage}`
    
    return (
        <div>
            <SideBar />
            
        <div className="container-wrapper">
             <h3 style={{margin: '10px 15px 5px'}}>Transformer Information</h3>
            <div className="grid-3 white-back">
               <Inputs title="Transformer Name" name={transformerName} />
               <Inputs title="Transformer Owner" name={transformerOwner} />
               <Inputs title="Transformer Rating" name={transformerRating} />
               <Inputs title="Transformer Capacity" name={transformerCapacity} />
               <Inputs title="% of Inpedant Loss" name={inpedentLoss} />
               <Inputs title="Average Houly Supply" name={averageHourlySupply} />
            </div>

            <div className="grid-3 white-back">
                <Inputs title="Asset Address" name={transformerAddress} />
               <Inputs title="Google Map Coords" name={googleMapCoords} />
               <Inputs title="Location Coords" name={bandDescription} />
               <Inputs title="Region" name={region} />
               <Inputs title="Area Office" name={office} />
               <Inputs title="Service Center" name={serviceCenter} />
               <Inputs title="Code" name={code} />
               <Inputs title="Supply Feeder Name" name={feederName} />
               <Inputs title="Capped Unit" name={cappedUnit} />

            </div>

            <div className="grid-3 white-back">
                <Inputs title="Connection Type" name={connectionType} />
               <Inputs title="DT Type" name={dtType} />
               <Inputs title="DT Meter No" name={dtMeterNo} />
            </div>

            <div style={{ display: 'flex', justifyContent: 'space-evenly',
                         alignItems: 'center', marginTop: 20, marginBottom: 20
                }}>

                <button className="btn" onClick={() => setPro(true)}>View Transformer Image</button>
        
            </div>
              

        </div>

        {pro && <div className="absolutes">
            <div className="img-views">
                <p className="close" title="Close" onClick={() => setPro(false)}>x</p>
                <p style={{position: 'absolute', padding: '10px', color: '#f2f2f2'}}>{googleMapCoords}</p>
                <img src={img} alt="Property Portrait" className="img-g" />
            </div>
        </div> }

        </div>
    )
}


export default TransCaptured
